import React from 'react';
import { List } from 'react-bootstrap-icons';
import { PuzzleFill } from 'react-bootstrap-icons';
import { ClipboardX } from 'react-bootstrap-icons';
import { Sliders } from 'react-bootstrap-icons';
import { StickyFill } from 'react-bootstrap-icons';
import PopUp from '../popup/popup';
import './toolbar.css';

function Toolbar() {
  return (
    <div className="toolbar">
       <a className='icon-tool' onClick={morePlubgin} type='button'><PuzzleFill ></PuzzleFill></a>
       <a className='icon-tool' onClick={copyClipboard} type='button'><ClipboardX ></ClipboardX></a>
       <a className='icon-tool' onClick={reClassCode} type='button'><Sliders ></Sliders></a>
       <a className='icon-tool' onClick={editDescription} type='button'><StickyFill ></StickyFill></a>
       <a className='icon-tool' onClick={quickMenu}  type='button'><List ></List></a>

       <PopUp 
        ></PopUp>
    </div>
    
  );
}

function morePlubgin(){
  <PopUp name ={'babc'}></PopUp>
}
function copyClipboard(){
  window.alert("more pubgin");
}
function reClassCode(){
  window.alert("more pubgin");
}
function editDescription(){
  window.alert("more pubgin");
}
function quickMenu(){
  window.alert("more pubgin");
}
export default Toolbar;
